<template>
  <div>
    <h3></h3>
    <hr/>
    <form @submit.prevent="getContentSubmit">
      <textarea placeholder="120;" v-model="content" maxlength="120"></textarea>
      <input type="submit" value=""/>
    </form>

    <ul>
      <li v-for="(item,index) in commentList" :key="item.add_time">
        <span>{{ index+1 }} :{{ item.user_name }} {{ item.add_time|dataFormat }}</span>
        <p>{{item.content=='undefined'?'':item.content}}</p>
      </li>

    </ul>
    <button type="button" class="mui-btn mui-btn-warning mui-btn-outlined" @click="getMore">
      
    </button>
  </div>

</template>

<script>
  import axios from 'axios'
  import {Toast} from 'mint-ui';

  export default {
    name: "comment",
    data() {
      return {
        commentList: [],
        index: 1,
        content: ''
      }
    },
    props: ['newsId'],
    created() {
      this.getCommentList()
    },
    methods: {
      getCommentList() {
        axios.get('http://www.liulongbin.top:3005/api/getcomments/' + this.newsId + '?pageindex=' + this.index).then(resu => {
          if (resu.data.status == 0) {
            this.commentList = this.commentList.concat(resu.data.message)
          } else {
            Toast({
              message: '',
              duration: 2000
            });
          }
        })

      },
      getMore() {
        this.index++;
        this.getCommentList()
      },

      getContentSubmit() {
        if (this.content.trim()) {
          axios.post('http://www.liulongbin.top:3005/api/postcomment/' + this.newsId, {
            content: this.content.trim()
          }).then(resu => {
            if (resu.data.status == 0) {
              this.commentList.unshift({
                add_time: Date.now(),
                user_name:'',
                content:this.content.trim()
              })
              this.content=''
              console.log(resu);
              // Toast({
              //   message: '',
              //   duration: 2000
              // });
            } else {
              Toast({
                message: '',
                duration: 2000
              });
            }
          })
        }

      }
    },


  }
</script>

<style scoped>
  textarea {
    margin: 0;
  }

  p {
    margin: 0;
    padding: 0;
  }

  ul, li {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  li {
    padding: 5px;
    font-size: 12px;
  }

  li span {
    background-color: #ccc;
    display: block;
    /*padding: 5px 20px;*/
  }

  li p {
    padding: 0 20px;
  }

  input {
    width: 100%;
  }

  button {
    width: 100%;
  }

</style>



